import React from 'react';
import NormalizedLink from './NormalizedLink';

const Footer = () => {
  const currentYear = new Date().getFullYear();
  const commit = process.env.REACT_APP_COMMIT_SHA || 'dev';
  const shortCommit = commit ? commit.substring(0, 7) : 'dev';
  const buildEnv = process.env.NODE_ENV;

  return (
    <footer id="contact" className="bg-slate-800 text-white pt-16 pb-8">
      <div className="container mx-auto px-6">
        <div className="grid grid-cols-1 md:grid-cols-3 lg:grid-cols-5 gap-8 text-center md:text-left">
          <div>
            <h3 className="text-xl font-bold mb-4 text-white">Quiet Strength</h3>
            <p className="text-slate-200">Empowering introverted women to thrive.</p>
          </div>
          <div>
            <h3 className="text-xl font-bold mb-4 text-white">Quick Links</h3>
            <nav className="flex flex-col space-y-2">
              <NormalizedLink 
                to="/#about"
                className="text-slate-200 hover:text-white text-left"
              >
                About
              </NormalizedLink>
              <NormalizedLink 
                to="/#themes"
                className="text-slate-200 hover:text-white text-left"
              >
                Themes
              </NormalizedLink>
              <NormalizedLink 
                to="/blog"
                className="text-slate-200 hover:text-white text-left"
              >
                Blog
              </NormalizedLink>
              <NormalizedLink 
                to="/#books"
                className="text-slate-200 hover:text-white text-left"
              >
                Books
              </NormalizedLink>
            </nav>
          </div>
          <div>
            <h3 className="text-xl font-bold mb-4 text-white">Legal</h3>
            <nav className="flex flex-col space-y-2">
              <button 
                className="text-slate-200 hover:text-white text-left"
                aria-label="View privacy policy"
              >
                Privacy Policy
              </button>
            </nav>
          </div>
        </div>
        <div className="mt-12 border-t border-slate-700 pt-8 text-center text-slate-200">
          <p>&copy; {currentYear} Quiet Strength. All Rights Reserved.</p>
          <p className="mt-2 text-xs text-slate-400">Build: {buildEnv}  Commit: {shortCommit}</p>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
